<template>
  <div id="me">
    <div >
      <img :src="img_src[parseInt(myAvatar)]" id="avatar">
    </div>
    <div >
      <h2 style="font-style: italic">name: {{myName}}</h2>
    </div>
    <div >
      <h2 style="font-style: italic">IP Address:{{myAddr}}</h2>
    </div>
    <div style="font-style: italic">
      <h2 v-if="mySex === 0" >sex:男</h2>
      <h2 v-else>sex:女</h2>
    </div>
    <div>
      <button @click="logout" id="logout">log out</button>
    </div>
    <div id="delete">
      <button @click="del" >delete</button>
      <button @click="delStorage" >clear cache</button>
    </div>
  </div>
</template>

<script>
  import {request2} from "../../network/request";

  export default {
    name: "DeleteUser",
    data() {
      return {
        myName: this.$store.state.globalUserName,
        mySex: this.$store.state.sex,
        myAvatar: this.$store.state.avatar,
        myAddr: this.$store.state.IpAddr,
        img_src:
          [require('assets/img/login_ava/boy.png'),
            require('assets/img/login_ava/girl.png'),
            require('assets/img/login_ava/geek.png'),
            require('assets/img/login_ava/alien.png'),
            require('assets/img/login_ava/group.png'),
            require('assets/img/login_ava/robot.png')
          ]
      }
    },
    methods: {
      del() {
        request2({
          url: this.$store.state.globalUrl + "/cleanalluser",
          method: 'get'
          }).then(res => {
            console.log('successfully delete')
          this.$router.replace('/home')
        }).catch(err => {
          console.log('fail to delete')
          this.$router.replace('/home')
        })
      },
      delStorage() {
        sessionStorage.clear()
      },
      logout() {
        request2({
          url: this.$store.state.globalUrl + '/logout',
          params: {
            username: this.$store.state.globalUserName
          }
        }).then(res => {
          // this.$store.commit('popMessager')
          // console.log(this.$store.state.listenMembers)
          this.$router.replace('/home')
        }).catch(err => {
          alert("下线失败")
        })
      }
    },
    destroyed() {
      this.logout()
    }
  }
</script>

<style scoped>
  #me{
    height:620px;
    background-color: white;
    display: flex;
    text-align: center;
    justify-content: space-around;
    flex-direction: column
  }

  #logout{
    width: 100px;
    height: 50px;
    border-radius: 20px;
    background-color: #4db6ac;
    font-size: 16px;
    font-weight: bold;
  }

  #avatar{
    width: 200px;
    height: 200px;
    border: 1px black solid;
    border-radius: 50%
  }
</style>



